.form {
  max-width: 30%;
  max-height: 100%;
  box-shadow: $box-shadow;
  @include flex(flex-start, center);
  flex-direction: column;
  padding: 2rem 2rem;
  margin: 0 auto;
  border-radius: 0.3rem;

  &__title {
    font-size: 2rem;
    text-transform: capitalize;
    text-align: center;
    width: 100%;
    // color: ;
  }
  &__label{
    font-size: 1rem;
    text-transform: capitalize;
    font-weight: 500;
    margin-top: 1.2rem
  }
  &__input {
    font-size: 1rem;
    width: 100%;
    outline: none;
    padding: 0.2rem 0.5rem;
    margin-top: 0.1rem;
    border: none;
    background-color: #F3F6F8;
    border-radius: 0.3rem;
    line-height: 2;
  }

  &__btn {
    margin-top: 1.5rem;
    text-align: center;
    width: 100%;
    @include flex(flex-start,center);
    gap: 0 1rem;
  
    & * {
      background-color: $main-color;
      outline: none;
      font-size: 1rem;
      border: none;
      border-radius: 0.3rem;
      padding: 0.2rem;
      color: white;
      font-weight: 500;
      
    }
  }
}