.cart {
    @include flex(flex-start, space-between);

    @include tablet {
        flex-wrap: wrap;
    }

    &__info {
        width: 40%;
        margin-left: 30px;
        margin-top: 30px;
        padding: 20px;
        box-shadow: $box-shadow;
        position: sticky;
        top: calc(#{$header-shrink-height} + 20px);

        @include tablet {
            width: 100%;
            margin: 30px 0 0;
            position: unset;
        }

        &__txt {
            & > * {
                margin-bottom: 10px;
            }

            p {
                font-size: 1.2rem;
            }

            &__price {
                @include flex(center, space-between);
                padding: 10px 0;
                font-size: 1rem;

                span:nth-child(2) {
                    font-size: 1.25rem;
                    color: $main-color;
                    font-weight: 600;
                }
            }

        }

        &__btn * {
            border-radius: unset;
        }
        &__btn > * {
            margin-bottom: 20px;
        }
    }

    &__list {
        width: 750px;
        background-color: $main-bg;
        box-shadow: $box-shadow;

        @include tablet {
            width: 100%;
        }

        &__title{
            font-size: 1.2rem;
            font-weight: 500;
            padding: 10px 0;
            @include flex(center, space-around);
            border-bottom: 1px solid $txt-second-color;

            @include mobile {
                display: none;

                & > * {
                    margin-bottom: 15px;
                }
            }
        }
    }

    &__item {
        @include flex(flex-start, flex-start);
        margin: 20px 0;
        position: relative;

        @include mobile {
            margin-bottom: 40px;
        }

        &__image {
            margin: 0 20px;
            width: 100px;
        }

        &__info {
            @include flex(flex-start, space-between);
            width: 100%;
            margin-right: 20px;
            flex-grow: 1;
            
            
            @include mobile {
                @include flex(flex-start, center);
                flex-direction: column;
            }
        }

        &__delete {
            position: absolute;
            bottom: 0;
            left: 20%;
            font-size: 1.5rem;

            i {
                cursor: pointer;

                &:hover {
                    color: $main-color;
                }
            }

            @include mobile {
                position: unset;
                margin-right: 10px;
            }
        }
    }
}