.catalog {
    @include flex(flex-start, flex-start);
    
    &__filter {
        width: 20%;

        @include tablet {
            width: max-content;
            height: 100vh;
            background: $main-bg;
            padding: 1rem;
            position: fixed;
            left: 0;
            top: $header-tablet-height;
            z-index: 97;
            box-shadow: $box-shadow;
            transform: translate(-100%);
            transition: transform 0.3s ease;
        }

        @include mobile {
            top: $header-mobile-height;            
        }

        &.active {
            transform: translateX(0);
        }

        &__widget {
            margin-bottom: 2rem;

            &__title {
                font-size: 1.1rem;
                font-weight: 600;
                text-transform: capitalize;
                margin-bottom: 16px;
            }
            &__content {
                color: $txt-second-color;

                &__item {
                    margin-bottom: 13px;
                }
            }
        }

        &__close {
            display: none;

            @include tablet {
                display: block;
                font-size: 2rem;
                margin-bottom: 1rem; 
            }
        }

        @include tablet {
            width: unset;
        }
    }

    &__content {
        flex-grow: 1;

        @include tablet {
            width: 100%;
            padding-top: 5rem;
        }
        @include mobile {
            width: 100%;
            padding-top: unset;
        }
    }

    &__filter__toggle {
        display: none;

        @include tablet {
            display: block;
            margin-bottom: 2rem;
            position: fixed;
            z-index: 96;
            top: 8%;
        }

        @include mobile{
            display: block;
            margin-bottom: 2rem;
            position: fixed;
            z-index: 96;
            top: 10%;
        }
    }
    
}